.search-form {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  padding: 0 $gap / 2;
  min-width: 250px;

  &__input {
    width: 80%;
    height: auto;
    margin-right: $gap / 2;
    padding: 0 $gap / 2;
    border-radius: $b-radius;

    &:hover,
    &:focus {
      border: 2px solid $color;
    }
  }
}